<template>
    <div>
      <div class="root">
        <div class="box">
          <el-form ref="form" :model="form" label-width="80px" :rules="pubFormRules">
              <el-form-item label="器官名称" class="change-label-calss" prop="title">
                  <el-input v-model="form.title" placeholder="请书写文章标题"></el-input>
              </el-form-item>
              <el-form-item label="器官概述" class="change-label-calss" prop="organdesc">
                  <el-input v-model="form.organdesc" placeholder="请书写文章概述"></el-input>
              </el-form-item>
          </el-form>
  
          <el-form ref="form" :model="form" label-width="80px" size="medium">
              <quill-editor v-model="form.content"></quill-editor>
          </el-form>
  
          <div class="submitbtn">
              <el-row>
                  <el-button @click="resetFn()">清空</el-button>
                  <el-button type="primary" @click="pubArticleFn()">提交</el-button>
                  <el-button type="info" @click="pubArticleFn('草稿')">保存草稿</el-button>
              </el-row>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { addOrganInfo } from '@/api'
  export default {
      data(){
          return{
            form:{
              title:'',
              organdesc:'',
              content:''
            }
          }
      },
      mounted(){
  
      },
      methods:{
        async pubArticleFn(){
          if(this.form.content == ''){
              this.$message({
                  type: 'warning',
                  message: '文章正文内容不能为空！'
              })
          }else if(this.form.organname == ''){
            this.$message({
                type: 'warning',
                message: '文章正文内容不能为空！'
            })
          }else if(this.form.organdesc == ''){
            this.$message({
                type: 'warning',
                message: '文章正文内容不能为空！'
            })
          }else{
              const { data: res } = await addOrganInfo(this.form)
              if(res.status !== 0) return this.$message.error(res.message)
              this.$message.success(res.message)
          }
        }
      }
  }
  </script>
  
  <style scoped>
  .root{
      height: 1900px;
      width: 100%;
      background: pink;
      padding-top: 100px;
  }
  .box{
    background: #fff;
    width: 90%;
    min-height: 500px;
    margin: 0 auto;
  }
  .submitbtn{
  width: 100%;
  }
  .submitbtn .el-button{
  float: right;
  margin-right: 20px;
  }
  ::v-deep .ql-editor{
      min-height: 300px;
      background-color: #ddd;
  }
  </style>